.flip-clock {
  gap: 10px;
  display: flex;
}

.digit {
  perspective: 1000px;
  background-color: #979797;
  width: 50px;
  height: 80px;
  position: relative;
  transform: rotateY(30deg);
}

.container {
  perspective: 800px;
  width: 200px;
  height: 200px;
  transform-style: preserve-3d;
}

.child {
  width: 100px;
  height: 100px;
  transform-style: preserve-3d;
  background: red;
  transform: rotateY(45deg);
}
